.loading{
    width: 100px;
    height: 100px;
    margin-bottom: 80px;
}

.loading img{
    width: 100%;
    height: 100%;
    animation: rotate 1.5s infinite linear;
}

@keyframes rotate{
    from{
        transform: rotate(0);
    }
    to{
        transform: rotate(360deg);
    }
}